.graph {
  margin-top: 50px;
  width: 100%;
  height: 100%;
  -webkit-box-shadow: 0 2px 4px #999, 0 2px 4px #999;
          box-shadow: 0 2px 4px #999, 0 2px 4px #999;
  padding: 20px;
  background-color: white;
  margin-bottom: 30px;
  font-size: 5px;
  color: #124257;
}

button {
  height: 30px;
  width: 100px;
  margin: 5px;
  border-radius: 5px;
  background-color: #124257;
  color: white;
  font-size: 16px;
  font-family: "Open Sans";
  cursor: pointer;
}

button:hover{
  background-color: #023247;
}

button:focus,
button:active {
  outline: none;
}

#btnRow {
  padding-left: 50px;
}



